// import guid from "generate-unique-id";
import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { Context as FormContext } from '../../context/FormContext';
import { Context as DatesContext } from '../../context/DatesContext';
import { addYears, formatWithOptions } from 'date-fns/fp';

import { useContext } from 'react';

import { FormInput } from './FormInput';
import { es, ms } from 'date-fns/locale';
import { TextField } from '@mui/material';
import { areIntervalsOverlapping } from 'date-fns';
import uuid from 'react-uuid';

export const Form = () => {
	// const id = useId();
	const {
		state: { calendar: { intervalRentedArray } },
		addIntervalToArrayRentedMsg,
		sendIntervalForSocket
	} = useContext(DatesContext);

	const {
		startMsg,
		endMsg,
		phoneMsg,
		nameMsg,
		emailMsg,
		roomMsg,
		priceMsg,
		showFormMsg,
		nightsNumberMsg
	} = useContext(FormContext);
	const { intervalRented, showForm } = useContext(FormContext).state;

	const { name, email, phone, room, start, end, nights, price, priceNights } = intervalRented;
	const [ isOverlapping, setIsOverLapping ] = useState(false);
	const [ data, setData ] = useState({});
	const [ formSubmitted, setFormSubmitted ] = useState(false);
	const [ roomId, setRoomId ] = useState('');

	useEffect(
		() => {
			nightsNumberMsg(nights);
		},
		[ nights ]
	);
	useEffect(
		() => {
			console.log('start end shitt');
			setRoomId(uuid());
			const intervalRentedArray_Date = intervalRentedArray.some(
				(interval) =>
					areIntervalsOverlapping(
						{ start: new Date(interval.end), end: new Date(interval.start) },
						{ start, end }
					) && interval.room === room
			);
			setIsOverLapping(intervalRentedArray_Date);
		},
		[ start, end ]
	);

	useEffect(
		() => {
			// var HOST = window.location.origin.replace(/^http/, 'ws');
			// const socketUrl = `${HOST}/wss`;
			// const wss = new WebSocket(socketUrl);
			const wss = new WebSocket(`ws://localhost:8000/wss`);

			if (formSubmitted) {
				console.log(data.id);
				console.log(roomId);

				// Open the WebSocket connection when this function is called
				// const uuid = uuid();

				wss.onopen = async () => {
					console.log('opwn con');
					console.log(data.id);
					wss.send(JSON.stringify(data));
					// ws.send('hello');
				};
				wss.onmessage = (msg) => {
					console.log(JSON.parse(msg.data));
					const { data } = JSON.parse(msg.data);
					const superData = JSON.parse(data);
					console.log(superData);

					addIntervalToArrayRentedMsg({
						...superData,
						start: new Date(superData.start),
						end: new Date(superData.end)
					});

					showFormMsg(false);
				};
			}
			// return () => {
			// 	ws.close();
			// };
		},
		[ formSubmitted ]
	);

	const handleSubmit = async (e) => {
		e.preventDefault();
		// if (!isOverlapping) {
		// const res = await axios.post(`http://localhost:8000/rented`, {
		// 	id: 'pinga',
		// 	name,
		// 	email,
		// 	phone,
		// 	room: room.toString(),
		// 	start: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(start),
		// 	end: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(end),
		// 	price: '2000',
		// 	nights
		// });
		const res = await axios.post(`/rented`, {
			id: 'pinga',
			name,
			email,
			phone,
			room: room.toString(),
			start: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(start),
			end: formatWithOptions({ locale: es }, 'yyyy/MM/dd')(end),
			price: '2000',
			nights
		});

		const dat2 = JSON.parse(res.config.data);

		const { $oid: id } = res.data.insertedId;

		const data = {
			...dat2,
			id
		};
		setData(data);
		// }
		// sendIntervalForSocket(data);

		setFormSubmitted(true);
	};

	return (
		<div style={{ width: '100%', backgroundColor: 'blue', padding: '20px' }}>
			<form noValidate autoComplete="off" onSubmit={handleSubmit}>
				<div style={{ display: 'flex', justifyContent: 'space-around' }}>
					<div>
						<FormInput
							value={start}
							handleChange={(newVal) => {
								startMsg(newVal);
							}}
							type="date"
							label="Desde"
						/>

						<FormInput value={end} handleChange={(newVal) => endMsg(newVal)} type="date" label="Hasta" />
						<FormInput
							value={nights}
							handleChange={(e) => nightsNumberMsg(e.target.value, e)}
							type="number"
							label="Noches"
						/>
						<FormInput
							value={phone}
							handleChange={(e) => phoneMsg(e.target.value)}
							type="text"
							label="Horario de Llegada"
						/>
					</div>
					<div>
						<FormInput
							value={room}
							handleChange={(e) => roomMsg(e.target.value)}
							type="text"
							label="Habitacion"
						/>
						<FormInput
							value={name}
							handleChange={(e) => nameMsg(e.target.value)}
							type="text"
							label="Nombre"
						/>
						<FormInput
							value={email}
							handleChange={(e) => emailMsg(e.target.value)}
							type="text"
							label="Email"
						/>
						<FormInput
							value={phone}
							handleChange={(e) => phoneMsg(e.target.value)}
							type="text"
							label="Telefono"
						/>
					</div>
					<div>
						<FormInput
							value={price}
							handleChange={(e) => priceMsg(e.target.value)}
							type="text"
							label="Precio"
						/>
						<FormInput
							value={priceNights}
							handleChange={(e) => nameMsg(e.target.value)}
							type="text"
							label="Total"
						/>
					</div>

					<div />
				</div>

				<button type="submit">SAVE </button>
			</form>
		</div>
	);
};
