import React, { useContext, useState, useEffect, useRef } from 'react';
import { Container } from './Container';
import { Context as FormContext } from '../../context/FormContext';
import { Context } from '../../context/DatesContext';

export const ContainerContainer = () => {
	const socket = useRef();
	const socketNew = useRef();

	const { socketDataMsg } = useContext(FormContext);
	// var HOST = window.location.origin.replace(/^http/, 'ws');
	// const wss = `${HOST}/wss`;
	// const ws = `${HOST}/ws`;
	// ('http://localhost:8000/ws');

	// ('http://localhost:8000/wss');

	// const ws = new WebSocket(socketUrl);
	socket.current = new WebSocket('http://localhost:8000/ws');
	// socket.current = new WebSocket(ws);

	// const

	socket.current.onopen = () => {
		console.log('WebSocket connection opened----------------------');
	};

	socket.current.onclose = () => {
		console.log('WebSocket connection closed++++++++++++++++++++++++++++++++++++++++');
	};
	// socketNew.current = new WebSocket(wss);
	socketNew.current = new WebSocket('http://localhost:8000/wss');

	// const

	socketNew.current.onopen = () => {
		console.log('WebSocket connection opened----------------------');
	};

	socketNew.current.onclose = () => {
		console.log('WebSocket connection closed++++++++++++');
	};

	useEffect(() => {
		socketDataMsg(socket.current);
	}, []);

	return (
		<div>
			<Container wss={socketNew.current} ws={socket.current} />
		</div>
	);
};
