import React, { useContext, useEffect, useState, useCallback, useRef } from 'react';
import { ScrollBar } from '../Scrollbar/ScrollBar';
import { Grid } from '../Grid/Grid';
import { NavDays } from '../NavDays/NavDays';
import { Context } from '../../context/DatesContext';
import { Context as FormContext } from '../../context/FormContext';
import axios from 'axios';
import TextField from '@mui/material/TextField';
import { Context as ThumbContext } from '../../context/ThumbContext';
import { FormContainer } from '../Form/FormContainer';
import { Gallery } from '../pagina/Gallery';

import _ from 'lodash';
import zIndex from '@mui/material/styles/zIndex';

export const Container = ({ wss, ws }) => {
	const { containerWidthMsg } = useContext(ThumbContext);
	const containerRef = useRef();

	const { state, loadIntervals_db_msg, addIntervalToArrayRentedMsg, removeByDragMsg } = useContext(Context);
	const { calendar } = state;
	const { intervalRented } = calendar;

	// const { intervalData } = state;
	const { showForm, formSubmitted } = useContext(FormContext).state;
	const { showFormMsg } = useContext(FormContext);

	const getRentedArray = async () => {
		// const res = await axios.get(`http://localhost:8000/all`);
		const res = await axios.get(`/all`);

		loadIntervals_db_msg(res.data);
	};

	// const ws = new WebSocket(`ws://localhost:8000/ws`);

	useEffect(() => {
		getRentedArray();
	}, []);
	const [ data, setData ] = useState({
		email: '',
		end: '2023/01/13',
		id: '63b58f79e18806c7473f348f',
		name: '',
		nights: '1',
		phone: '',
		price: '2000',
		room: '0',
		start: '2023/01/12'
	});
	function setInitialDragRented(superData) {
		removeByDragMsg({
			...superData,
			start: new Date(superData.start),
			end: new Date(superData.end)
		});
	}
	function setInitialNewRented(superData) {
		addIntervalToArrayRentedMsg({
			...superData,
			start: new Date(superData.start),
			end: new Date(superData.end)
		});
	}

	useEffect(() => {
		wss.onopen = () => {
			console.log('NEW RENTED CONTAINER SOCKET OPENNN');
			wss.send(
				JSON.stringify({
					...data,
					start: '2023/01/15',
					end: '2023/01/16'
				})
			);
		};

		wss.onmessage = (msg) => {
			console.log('NEW RENTED CONTAINER CONTAINER SOCKET ONMESSAGE');

			const { data } = JSON.parse(msg.data);

			const superData = JSON.parse(data);

			setInitialNewRented(superData);
			wss.close();
		};
	}, []);

	useEffect(() => {
		console.log('DRAG CONTAINER CONTAINER SOCKET ONOPEN');
		ws.onopen = () => {
			ws.send(JSON.stringify(data));
		};

		ws.onmessage = (msg) => {
			console.log('DRAGGG CONTAINER CONTAINER SOCKET ONMESSAGE');

			const { data } = JSON.parse(msg.data);

			const superData = JSON.parse(data);

			// setInitialNewRented(superData);
			setInitialDragRented(superData);
			ws.close();
			// ws.close(z);
			// showForm ? setInitialNewRented(superData) : setInitialDragRented(superData);
		};
	}, []);

	return (
		<div
			style={{
				display: 'flex',
				justifyContent: 'center',
				alignItems: 'center'
			}}
		>
			<div
				ref={containerRef}
				style={{
					display: 'flex',
					flexDirection: 'column',
					width: '80%'
				}}
			>
				<div style={{ display: 'flex', flexDirection: 'column' }}>
					{/* <Gallery /> */}
					<Grid ws={ws} />
				</div>

				{showForm ? (
					<div
						style={{
							width: '60%',
							position: 'absolute',
							left: '10%',
							top: '7%',

							backgroundColor: 'blue'
						}}
					>
						<FormContainer wss={wss} />
					</div>
				) : null}

				<div style={{}}>
					<ScrollBar />
				</div>
				<NavDays />
				<button />
			</div>
		</div>
	);
};
